.blocker {
  height: 100vh;
  width: 100vw;
  z-index: 9999;
  pointer-events: none;
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
}
.loader {
  position: fixed;
  left: 50%;
  top: 50%;
  // margin: 100px auto;
  font-size: 22px;
  width: 0.8em;
  height: 0.8em;
  border-radius: 50%;
  // position: relative;
  text-indent: -9999em;
  -webkit-animation: load5 1.1s infinite ease;
  animation: load5 1.1s infinite ease;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
@-webkit-keyframes load5 {
  0%,
  100% {
    box-shadow: 0em -2.6em 0em 0em #ff6b01,
      1.8em -1.8em 0 0em rgba(255, 107, 1, 0.2),
      2.5em 0em 0 0em rgba(255, 107, 1, 0.2),
      1.75em 1.75em 0 0em rgba(255, 107, 1, 0.2),
      0em 2.5em 0 0em rgba(255, 107, 1, 0.2),
      -1.8em 1.8em 0 0em rgba(255, 107, 1, 0.2),
      -2.6em 0em 0 0em rgba(255, 107, 1, 0.5),
      -1.8em -1.8em 0 0em rgba(255, 107, 1, 0.7);
  }
  12.5% {
