.backdrop__students-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 1;
  z-index: 10;
  transition: opacity 250ms linear, transform 250ms linear;
}

.modal {
  @media screen and (max-width: 767.98px) {
    width: 60%;
  }
  overflow-y: auto;
  overflow-x: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
  width: 50%;
  height: 95%;
  padding: 30px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  transition: opacity 250ms linear, transform 250ms linear;
}

.close-btn {
  background-color: #fff;
  border-radius: 50%;
  border-color: rgb(180, 22, 22);
  position: absolute;
  top: 0px;
  right: 0px;
}

.modal__descr-container {
  display: flex;
  flex-direction: column;
}

.modal__title {
  font-size: 35;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  color: rgba(194, 22, 22, 0.849);
  margin-bottom: 15px;
  padding: 5px;
  padding-top: 0;
}

.modal__descr {
  font-size: 14px;
  text-align: center;
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 5px;
  padding-top: 5px;
  font-weight: 500;
}

.modal__descr--position {
  font-size: 12px;
  font-weight: 400;
}

.modal__team {
  display: flex;
  flex-wrap: wrap;
  @media screen and (max-width: 767.98px) {
    margin-left: 0;
  }
  @media screen and (min-width: 768px) and (max-width: 1279.98px) {
    margin-left: -20px;
  }
  margin-left: -30px;
}

.modal__team-people {
  @media screen and (max-width: 767.98px) {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
    &:hover,
    &:focus {
      transform: scale(1.1);
    }
  }
  @media screen and (min-width: 768px) and (max-width: 1279.98px) {
    margin-left: 20px;
    width: calc((100% - 40px) / 2);
  }
  border: 1px gray solid;
  border-radius: 5px;
  margin-left: 30px;
  width: calc((100% - 90px) / 3);
  transition: 250ms transform linear;

  &:hover,
  &:focus {
    transform: scale(1.2);
  }
  margin-bottom: 30px;
  box-shadow: 4px 4px 9px -2px rgba(115, 96, 115, 1);
}
